<template>
  <div class="container mt-3">
    <small class="text-muted">{{ $options.__file }}</small>
    <h1 class="text-primary font-weight-bold">
      <router-link to="/"><img src="~bootstrap-icons/icons/caret-left-fill.svg" alt="caret icon" width="30" height="30"></router-link>
      Contatti
    </h1>
    <router-link :to="'/store/' + c.store_id + '/contact/' + c.id" v-for="c in contacts" :key="c.id" class="text-decoration-none">
      <Card :title="c.name || c.request" :caption="c.mtime" :icon="publicPath + 'icons/person.svg'"/>
    </router-link>
    <div class="alert-secondary p-2 mt-5" v-show="contacts.length == 0">Non ci sono contatti</div>
    <button type="button" class="btn btn-primary btn-lg float-right m-5" disabled>Aggiungi contatto (TODO)</button>
  </div>
</template>

<script>
import Card from '@/components/Card.vue'

export default {
  components: {
    Card
  },
  data () {
    return {
      publicPath: process.env.BASE_URL,
      contacts: []
    }
  },
  methods: {
    async fetchStores () {
      const response = await fetch('/api/stores', {
        mode: 'no-cors',
        headers: {
          'accept': 'application/json'
        }
      }
      )
      return response.json()
    },
    async fetchContacts (storeId) {
      const response = await fetch('/api/stores/' + storeId + '/contacts', {
        mode: 'no-cors',
        headers: {
          'accept': 'application/json'
        }
      }
      )
      return response.json()
    }
  },
  mounted () {
    let self = this
    this.fetchStores().then(data => {
      data.stores.forEach(function (store) {
        console.log('looking at ', store.id)
        self.fetchContacts(store.id).then(data => {
          data.contacts.forEach(c => {
            console.log('pushing ', c.request)
            self.contacts.push(c)
          }
          )
        }
        )
      }
      )
    }
    )
  }
}
</script>

